.homeHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;
	padding-top: 20px;
	&-logo {
		display: flex;
		align-self: center;
		overflow: auto;
		a {
			display: flex;
		}
		img {
			height: 60px;
			width: 60px;
			align-self: center;
		}
		h1 {
			padding: 0px 18px;
		}
	}
}
